import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import {
  getPracticeClassifications,
  deletePracticeClassifications,
} from 'services/practiceClassification';

class GradedExercisesPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePracticeClassifications(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Xp loi rn luyn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'T im',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'n im',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#graded/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Xp loi rn luyn">
        <CartHeadAction>
          <BtnAddNew href="#/graded/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPracticeClassifications}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GradedExercisesPage />
  </Page>
);



